import { async } from "regenerator-runtime";
import loginFunctions from "../../../scripts/utils/loginFunctions";


const mainLogin = {
    async render() {
        return ` <section class="vh-100" style="background-color: #fffff;">
        <div class="container h-100">
          <div class="row d-flex justify-content-center align-items-center h-100">
            <div class="col-lg-12 col-xl-11">
              <div class="card text-black" style="border-radius: 25px; border-color : #5f5f5f; border-width: 5px;">
                <div class="card-body p-md-5">
                  <div class="row justify-content-center">
                    <div class="col-md-10 col-lg-6 col-xl-5 order-2 order-lg-1">
      
                      <p class="text-center h1 fw-bold mb-5 mx-1 mx-md-4 mt-4" style="color: #064ea2;">Login Admin</p>
      
                      <form class="mx-1 mx-md-4" id="loginForm">
      
                        <div class="d-flex flex-row align-items-center mb-4">
                          <i class="fas fa-envelope fa-lg me-3 fa-fw"></i>
                          <div class="form-outline flex-fill mb-0">
                            <input type="email" id="form3Example3c" class="form-control" placeholder="Your Email" required />
                          </div>
                        </div>
      
                        <div class="d-flex flex-row align-items-center mb-4">
                          <i class="fas fa-lock fa-lg me-3 fa-fw"></i>
                          <div class="form-outline flex-fill mb-0">
                            <input type="password" id="form3Example4c" class="form-control" placeholder="Password" required />
                          </div>
                        </div>

                        <div class="form-check d-flex justify-content-center mb-4">
                        <label class="form-check-label" for="form2Example3">
                        Belum memiliki akun? silahkan <a href="register/">Registrasi Disini</a>
                        </label>
                        </div>

      
                        <div class="d-flex justify-content-center mx-4 mb-3 mb-lg-4">
                          <button type="submit" class="btn btn-primary btn-lg">Login</button>
                        </div>

                        <div class="d-flex justify-content-start mx-4 mb-3 mb-lg-4" style="position: absolute; top: 20px; left: 15px;">
                          <a href="../"><button type="button" class="btn btn-secondary btn-lg"><i class="fas fa-arrow-left"></i></button></a>
                        </div>
                    
                      </form>
      
                    </div>
                    <div class="col-md-10 col-lg-6 col-xl-7 d-flex align-items-center order-1 order-lg-2">
      
                      <img src="../assets/img/login_admin.png"
                        class="img-fluid" alt="Sample image">
      
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>`;
    },

    async afterRender(){
        console.log('afterrender jalan..');
        

        const loginForm=document.getElementById('loginForm');
        loginForm.addEventListener('submit', async(e) => {
            const inputEmail = document.getElementById('form3Example3c')
            const inputPassword = document.getElementById('form3Example4c')
            e.preventDefault();
            const data = {
              email: inputEmail.value,
              password: inputPassword.value
            } 
            await loginFunctions.init(data) 
        })
    }
}
export default mainLogin;